import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { KeepPage } from '../keep/keep';

/**
 * Generated class for the KeepListPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-keep-list',
  templateUrl: 'keep-list.html',
})
export class KeepListPage {
//  keeplist = {};
  public keeplist = [];
  public taglist = [];

  constructor(public navCtrl: NavController, public navParams: NavParams) {
    //this.keeplist = navParams.get('panel');
    //this.keeplist['title'] = ''

  }

  ionViewDidLoad() {
    // 
    this.taglist = ["#","#","#","#1","#"];

    //
    this.keeplist = [
        { title: "dress1", src: "assets/img/image01.jpg", description:"",color: "danger" },
        { title: "dress2", src: "assets/img/image05.jpg", description:"",color: "light" },
        { title: "dress3", src: "assets/img/image08.jpg", description:"",color: "light" },
        { title: "dress4", src: "assets/img/image02.jpg", description:"",color: "light" },
        { title: "dress5", src: "assets/img/image01.jpg", description:"",color: "light" }
    ];
  }

  goToKeep(task) {
    this.navCtrl.push(KeepPage, {keep: task});
  }
}




